/* animation selectors */
.Fade {
  animation: Fade 0.4s 1 ease;
}

.FadeInDown {
  animation: FadeInDown 0.4s 1 ease;
}

.FadeInDownSmall {
  animation: FadeInDownSmall 0.4s 1 ease;
}

.FadeInUp {
  animation: FadeInUp 0.4s 1 ease;
}

.FadeOut {
  animation: FadeOut 0.4s 1 ease;
}

.SlideInDown {
  animation: SlideInDown 0.4s 1 ease;
}

.SlideInLeft {
  animation: SlideInLeft 0.4s 1 ease;
}

.SlideInRight {
  animation: SlideInRight 0.4s 1 ease;
}

.SlideInUp {
  animation: SlideInUp 0.4s 1 ease;
}

.SlideOutDown {
  animation: SlideOutDown 0.4s 1 ease;
}

.SlideOutLeft {
  animation: SlideOutLeft 0.4s 1 ease;
}

.SlideOutRight {
  animation: SlideOutRight 0.4s 1 ease;
}

.Spin {
  animation: Spin 0.4s 1 ease;
}

@keyframes Fade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* animations */
@keyframes FadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -20px, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes FadeInDownSmall {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes FadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(20px, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes FadeInRight {
  from {
    opacity: 0;
    transform: translate3d(-20px, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes FadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 20px, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes FadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes SlideInDown {
  from {
    transform: translate3d(0, -100%, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes SlideInLeft {
  from {
    transform: translate3d(100%, 0, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes SlideInRight {
  from {
    transform: translate3d(-100%, 0, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes SlideInUp {
  from {
    transform: translate3d(0, 100%, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes SlideOutDown {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    transform: translate3d(0, -100%, 0);
  }
}

@keyframes SlideOutLeft {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    transform: translate3d(100%, 0, 0);
  }
}

@keyframes SlideOutRight {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    transform: translate3d(100%, 0, 0);
  }
}

@keyframes SlideOutUp {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    transform: translate3d(0, 100%, 0);
  }
}

@keyframes Spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
