.icon {
  display: inline-block;
  flex-shrink: 0;
  width: 1.3125rem;
  height: 1.3125rem;
  fill: currentColor;
  vertical-align: middle;
  transform-origin: center center;

  /* sizes */
  &--4 {
    width: 0.25rem;
    height: 0.25rem;
  }

  &--8 {
    width: 0.5rem;
    height: 0.5rem;
  }

  &--16 {
    width: 1rem;
    height: 1rem;
  }

  &--24 {
    width: 1.5rem;
    height: 1.5rem;
  }

  &--32 {
    width: 2rem;
    height: 2rem;
  }

  &--48 {
    width: 3rem;
    height: 3rem;
  }

  &--64 {
    width: 4rem;
    height: 4rem;
  }

  &--rotate-90 {
    transform: rotate(90deg);
  }

  &--rotate-180 {
    transform: rotate(180deg);
  }

  &--rotate-270 {
    transform: rotate(270deg);
  }

  &--rotate-360 {
    transform: rotate(360deg);
  }

  /* rounded */
  &--rounded {
    box-sizing: content-box;
    border-radius: var(--border-radius--round);
    padding: 0.325em;
  }

  /* loading */
  &--spinning {
    animation: Spin 0.4s infinite linear;
  }

  /* rotated */
  &--rotated {
    transition: var(--transition);
  }
}
