.transition {
  display: inline-block;

  /* types */
  &--fade {
    animation: Fade 0.4s 1 ease;
  }

  &--fade-in-down {
    animation: FadeInDown 0.4s 1 ease;
  }

  &--fade-in-left {
    animation: FadeInLeft 0.4s 1 ease;
  }

  &--fade-in-right {
    animation: FadeInRight 0.4s 1 ease;
  }

  &--fade-in-up {
    animation: FadeInUp 0.4s 1 ease;
  }

  &--fade-out {
    animation: FadeOut 0.4s 1 ease;
  }

  &--slide-in-down {
    animation: SlideInDown 0.4s 1 ease;
  }

  &--slide-in-left {
    animation: SlideInLeft 0.4s 1 ease;
  }

  &--slide-in-right {
    animation: SlideInRight 0.4s 1 ease;
  }

  &--slide-in-up {
    animation: SlideInUp 0.4s 1 ease;
  }

  &--slide-out-down {
    animation: SlideOutDown 0.4s 1 ease;
  }

  &--slide-out-Left {
    animation: SlideOutLeft 0.4s 1 ease;
  }

  &--slide-out-right {
    animation: SlideOutRight 0.4s 1 ease;
  }

  &--slide-out-up {
    animation: SlideOutUp 0.4s 1 ease;
  }

  &--spin {
    animation: Spin 0.4s 1 ease;
  }
}
