.thumb {
  flex-shrink: 0;
  display: inline-flex;
  width: var(--thumb-size);
  height: var(--thumb-size);
  font-size: var(--thumb-size);
  line-height: 0;
  position: relative;
  border-radius: var(--thumb-border-radius);
  background: var(--body-color-border);
  vertical-align: middle;

  img {
    border-radius: var(--thumb-border-radius);
    object-fit: cover;
    width: 100%;
    height: 100%;
    opacity: 1;
    transition: var(--transition);
  }

  &--xs {
    width: var(--thumb-size--xs);
    height: var(--thumb-size--xs);
    font-size: var(--thumb-size--xs);
  }

  &--sm {
    width: var(--thumb-size--sm);
    height: var(--thumb-size--sm);
    font-size: var(--thumb-size--sm);
  }

  &--md {
    width: var(--thumb-size--md);
    height: var(--thumb-size--md);
    font-size: var(--thumb-size--md);
  }

  &--lg {
    width: var(--thumb-size--lg);
    height: var(--thumb-size--lg);
    font-size: var(--thumb-size--lg);
  }

  &--ratio-16-9 {
    overflow: hidden;
    width: 100%;
    height: auto;
    border-radius: 0;
    font-size: var(--thumb-size--lg);

    img {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      border-radius: 0;
      bottom: 0;

      @supports (object-fit: cover) {
        object-fit: cover;
        bottom: auto;
      }
    }

    &::before {
      content: "";
      display: block;
      pointer-events: none;
      z-index: -1;
      padding-bottom: var(--thumb-size-ratio-16-9);
    }
  }

  /* letter inside */
  &__letter {
    font-size: 0.2em;
    color: rgba(255, 255, 255, 0.4);
    line-height: normal;
    display: flex;
    align-items: center;
    justify-content: center;
    word-break: break-word;
    text-align: center;
    width: 100%;
    height: inherit;
    padding: var(--card-padding--sm);
    font-weight: var(--font-weight--bolder);
    user-select: none;
  }

  /* states */
  &--loading {
    img {
      opacity: 0;
    }

    &::after {
      content: "";
      position: absolute;
      animation: Spin 0.4s infinite linear;
      height: 0.4em;
      width: 0.4em;
      left: calc(50% - 0.2em);
      top: calc(50% - 0.2em);
      border: 2px solid var(--color-white);
      border-right-color: transparent;
      border-top-color: transparent;
      border-radius: 5em;
    }
  }
}
