.modal {
  z-index: var(--z-index-100);
  height: 0;
  left: 0;
  top: 0;
  pointer-events: none;
  position: fixed;
  overflow: hidden;
  color: var(--modal-color);

  h1,
  .h1,
  h2,
  .h2,
  h3,
  .h3,
  h4,
  .h4,
  h5,
  .h5 {
    color: var(--modal-color--dark) !important;
  }

  /* backdrop */
  &__dialog {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--backdrop-color-bg);
    transition: var(--transition--backdrop);
    opacity: 0;
  }

  /* the modal window */
  &__content {
    opacity: 0;
    transform: translateY(50px);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    min-height: 0;
    background: var(--modal-color-bg);
    border-radius: var(--modal-border-radius);
    margin: var(--grid-gutter);
    max-width: calc(100% - var(--grid-gutter) * 2);
  }

  /* scrollable area */
  &__body {
    min-height: auto;
    flex: 0 1 auto;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    padding: var(--modal-padding);

    &::-webkit-scrollbar {
      width: 6px;
      background-color: transparent;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background-color: var(--modal-color-border);
    }

    .modal__header + & {
      padding-top: 1px;
    }

    @media (--viewport-md-min) {
      padding: var(--modal-padding--lg);
    }
  }

  /* header */
  &__header {
    z-index: var(--z-index-30);
    position: sticky;
    top: 0;
    flex: 0 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--modal-padding);

    &__item {
      display: flex;
      align-items: center;
      margin-right: var(--spacer);
    }

    &__image {
      margin-right: var(--spacer);
    }

    @media (--viewport-md-min) {
      padding: var(--modal-padding--lg);
    }
  }

  /* search */
  &__search {
    z-index: var(--z-index-30);
    position: sticky;
    top: 0;
    flex: 0 0 auto;
    padding: 0 var(--modal-padding) var(--spacer) var(--modal-padding);

    @media (--viewport-md-min) {
      padding: 0 var(--modal-padding--lg) var(--spacer) var(--modal-padding--lg);
    }
  }

  &__title {
    margin: 0;
    color: var(--modal-color--dark);

    & + .modal__close {
      margin-left: var(--spacer);
    }
  }

  &__close {
    padding: 0;
    opacity: 0.4;

    &:hover {
      background: transparent;
      opacity: 0.8;
    }

    &:active:focus {
      background: transparent;
      opacity: 1;
    }
  }

  /* footer */
  &__footer {
    flex: 0 0 auto;
    position: sticky;
    bottom: 0;
    padding: var(--modal-padding);

    @media (--viewport-md-min) {
      padding: var(--modal-padding--lg);
    }
  }

  /* active */
  &--active {
    right: 0;
    bottom: 0;
    pointer-events: auto;
    height: auto;

    & > .modal__dialog {
      opacity: 1;
    }

    & > .modal__dialog > .modal__content {
      opacity: 1;
      transform: translateY(0);
      transition: var(--transition);
    }
  }

  /* Sizes */
  &--sm {
    .modal__content {
      width: var(--modal-sm);
    }
  }

  &--md {
    .modal__content {
      width: var(--modal-md);
    }
  }

  &--lg {
    .modal__content {
      width: var(--modal-lg);
    }
  }

  &--xl {
    .modal__content {
      width: var(--modal-xl);
    }
  }

  &--fullscreen {
    & > .modal__dialog {
      background: var(--body-color-bg);
      align-items: stretch;
      justify-content: flex-start;
      transition: none;
    }

    & > .modal__dialog > .modal__content {
      transition: none;
      transform: none;
      min-height: 100%;
      max-width: none;
      margin: 0;
    }

    & > .modal__dialog > .modal__content > .modal__body {
      padding-left: 0;
      padding-right: 0;
    }

    & > .modal__dialog > .modal__content > .modal__header {
      background: var(--header-color-bg);
      color: var(--header-color);
      padding: var(--header-padding);
      box-shadow: var(--header-shadow);
    }

    & > .modal__dialog > .modal__content > .modal__header > .container {
      max-width: none;
    }
  }
}
