:root {
  /* Colors Shades */
  --color-white: #ffffff;
  --color-gray-1: #f2f3f4;
  --color-gray-2: #dadedf;
  --color-gray-3: #c1c7c9;
  --color-gray-4: #a7afb2;
  --color-gray-5: #8c989a;
  --color-gray-6: #6f7c80;
  --color-gray-7: #555f61;
  --color-gray-8: #373d3f;
  --color-gray-9: #131516;
  --color-gray-10: #101111;
  --color-black: #000000;

  /* Colors Theme */

  /* primary */
  --color-primary--light: #ff4d4f;
  --color-primary: #f5222d;
  --color-primary--dark: #cf1322;

  /* secondary */
  --color-secondary--light: #597ef7;
  --color-secondary: #2f54eb;
  --color-secondary--dark: #1d39c4;

  /* success */
  --color-success--light: #74e09f;
  --color-success: #00c851;
  --color-success--dark: #007e33;
  --color-success-bg: #f2fffb;

  /* danger */
  --color-danger--light: #f97f7f;
  --color-danger: #ff4444;
  --color-danger--dark: #cc0000;
  --color-danger-bg: #ffe1e1;

  /* warning */
  --color-warning--light: #ffd666;
  --color-warning: #faad14;
  --color-warning--dark: #d48806;
  --color-warning-bg: #faf0e5;

  /* info */
  --color-info--light: #adc6ff;
  --color-info: #85a5ff;
  --color-info--dark: #597ef7;
  --color-info-bg: #f0faff;

  /* facebook */
  --color-facebook: #3b5998;
  --color-facebook--light: #2c4a8b;
  --color-facebook--dark: #28437e;

  /* google */
  --color-google: #ea4335;
  --color-google--light: #da3728;
  --color-google--dark: #ce2718;

  /* Colors Body */
  --body-color--light: var(--color-gray-5);
  --body-color: var(--color-gray-7);
  --body-color--dark: var(--color-gray-10);
  --body-color--heading: var(--body-color--dark);
  --body-color-bg: var(--color-gray-1);
  --body-color-link: var(--color-secondary);
  --body-color-border: var(--color-gray-2);

  /* Color Backdrop for Modals */
  --backdrop-color-bg: rgba(23, 28, 34, 0.3);

  /* Text */
  --font-family: 'Montserrat', sans-serif;
  --font-size: 0.875rem;
  --font-size--sm: 0.75rem;
  --font-size--xs: 0.5625rem;
  --font-size--lg: 1rem;
  --font-weight: normal;
  --font-weight--bold: bold;
  --font-weight--bolder: 800;
  --font-line-height: 1.45;

  /* Font Sizes */
  --to-rem: 0.0625rem;
  --h1-min: 28;
  --h1-max: 36;
  --h2-min: 24;
  --h2-max: 30;
  --h3-min: 18;
  --h3-max: 21;
  --h4-min: 16;
  --h4-max: 18;
  --h5-min: 14;
  --h5-max: 14;
  --h1-responsive: calc((var(--h1-min) * var(--to-rem)) + (var(--h1-max) - var(--h1-min)) * ((100vw - 576px) / (1200 - 576)));
  --h2-responsive: calc((var(--h2-min) * var(--to-rem)) + (var(--h2-max) - var(--h2-min)) * ((100vw - 576px) / (1200 - 576)));
  --h3-responsive: calc((var(--h3-min) * var(--to-rem)) + (var(--h3-max) - var(--h3-min)) * ((100vw - 576px) / (1200 - 576)));
  --h4-responsive: calc((var(--h4-min) * var(--to-rem)) + (var(--h4-max) - var(--h4-min)) * ((100vw - 576px) / (1200 - 576)));
  --h5-responsive: calc((var(--h5-min) * var(--to-rem)) + (var(--h5-max) - var(--h5-min)) * ((100vw - 576px) / (1200 - 576)));

  /* Links */
  --link-decoration: none;
  --link-decoration--hover: underline;

  /* Media widths */
  --viewport-sm: 576px;
  --viewport-md: 768px;
  --viewport-lg: 992px;
  --viewport-xl: 1200px;

  /* Grid */
  --container-width: 1260px;
  --grid-gutter: 0.75rem;
  --grid-gutter--sm: 0.5rem;
  --spacer: 1rem;

  /* Borders Radius */
  --border-radius: 0.15rem;
  --border-radius--lg: 0.375rem;
  --border-radius--round: 50%;

  /* z-index */
  --z-index-10: 1010;
  --z-index-20: 1020;
  --z-index-30: 1030;
  --z-index-40: 1040;
  --z-index-50: 1050;
  --z-index-60: 1060;
  --z-index-70: 1070;
  --z-index-80: 1080;
  --z-index-90: 1090;
  --z-index-100: 1100;
  --z-index-110: 1200;

  /* Elevation Shadows */
  --depth-1: 0 1px 2px 0 rgba(30, 38, 51, 0.02), 0 2px 1px -2px rgba(30, 38, 51, 0.01), 0 1px 3px 0 rgba(30, 38, 51, 0.08);
  --depth-2: 0 4px 5px 0 rgba(30, 38, 51, 0.05), 0 1px 10px 0 rgba(30, 38, 51, 0.03), 0 2px 4px -1px rgba(30, 38, 51, 0.1);
  --depth-3: 0 8px 17px 2px rgba(30, 38, 51, 0.08), 0 3px 14px 2px rgba(30, 38, 51, 0.05), 0 5px 5px -3px rgba(30, 38, 51, 0.2);
  --depth-4: 0 20px 34px 4px rgba(30, 38, 51, 0.08), 0 6px 30px 8px rgba(30, 38, 51, 0.07), 0 10px 16px -7px rgba(30, 38, 51, 0.3);
  --depth-1--negative: 0 -1px 2px 0 rgba(30, 38, 51, 0.02), 0 -2px 1px -2px rgba(30, 38, 51, 0.01), 0 -1px 3px 0 rgba(30, 38, 51, 0.08);
  --depth-2--negative: 0 -4px 5px 0 rgba(30, 38, 51, 0.05), 0 -1px 10px 0 rgba(30, 38, 51, 0.03), 0 -2px 4px -1px rgba(30, 38, 51, 0.1);
  --depth-3--negative: 0 -8px 17px 2px rgba(30, 38, 51, 0.08), 0 -3px 14px 2px rgba(30, 38, 51, 0.05), 0 -5px 5px -3px rgba(30, 38, 51, 0.2);
  --depth-4--negative: 0 -20px 34px 2px rgba(30, 38, 51, 0.08), 0 -6px 30px 5px rgba(30, 38, 51, 0.07), 0 -8px 10px -7px rgba(30, 38, 51, 0.3);

  /* Transitions */
  --transition: all cubic-bezier(0.55, 0, 0.1, 1) 0.2s;
  --transition--slow: all cubic-bezier(0.55, 0, 0.1, 1) 0.7s;
  --transition--fast: all cubic-bezier(0.55, 0, 0.1, 1) 0.1s;
  --transition--collapse: max-height cubic-bezier(0.55, 0, 0.1, 1) 0.35s, opacity cubic-bezier(0.55, 0, 0.1, 1) 0.35s;
  --transition--navigation: all cubic-bezier(0.55, 0, 0.1, 1) 0.5s;
  --transition--backdrop: opacity cubic-bezier(0.55, 0, 0.1, 1) 0.5s;

  /* Form Inputs */
  --input-color: var(--color-gray-10);
  --input-color--highlight: var(--color-success);
  --input-color--highlight--light: var(--color-success-bg);
  --input-color--highlight--dark: var(--color-success--dark);
  --input-color-bg: var(--color-white);
  --input-color-bg-hover: var(--color-white);
  --input-color-bg--focus: var(--color-white);
  --input-color-bg--invalid: var(--color-danger-bg);
  --input-color-bg--valid: var(--color-success-bg);
  --input-color-bg--readonly: var(--color-white);
  --input-color-border: var(--color-gray-3);
  --input-color-border--focus: var(--color-secondary);
  --input-color-border--invalid: var(--color-danger);
  --input-color-border--valid: var(--color-success);
  --input-color-border--readonly: var(--input-color-border);
  --input-line-height: var(--font-line-height);
  --input-placeholder-color: var(--color-gray-5);
  --input-border-width: 1px;
  --input-border-radius: var(--border-radius);
  --input-padding-x: 0.625rem;
  --input-padding-y: 0.5rem;
  --input-padding-x--sm: 0.425rem;
  --input-padding-y--sm: 0.157rem;
  --input-padding-x--lg: 0.719rem;
  --input-padding-y--lg: 0.719rem;
  --input-transition: var(--transition--fast);
  --input-checkbox-size: 1.125rem;
  --input-icon-size: 1.125rem;
  --input-tip-color: var(--body-color);
  --input-tip-color-bg: var(--color-info-bg);
  --input-switch-height: 2rem;
  --input-switch-height-inner: 1.2rem;
  --input-shadow: var(--depth-1);
  --input-shadow--hover: var(--depth-2);

  /* Input Radius */
  --input-rounded-border-radius: 18px;

  /* Form Group */
  --form-group-margin: var(--spacer);
  --form-group-margin-label: calc(var(--spacer) / 4);

  /* Button */
  --btn-font-size: var(--font-size);
  --btn-font-size--sm: var(--font-size--sm);
  --btn-font-size--lg: var(--font-size--lg);
  --btn-padding-x: calc(var(--input-padding-x) * 2);
  --btn-padding-y: var(--input-padding-y);
  --btn-padding-x--sm: calc(var(--input-padding-x--sm) * 1.5);
  --btn-padding-y--sm: var(--input-padding-y--sm);
  --btn-padding-x--lg: calc(var(--input-padding-x--lg) * 3);
  --btn-padding-y--lg: var(--input-padding-y--lg);
  --btn-line-height: var(--input-line-height);
  --btn-transition: var(--transition--fast);
  --btn-border-width: var(--input-border-width);
  --btn-border-radius: var(--input-border-radius);
  --btn-shadow: var(--depth-1);
  --btn-shadow--hover: var(--depth-2);
  --btn-fab-width: 4rem;
  --btn-fab-width--sm: 2.5rem;
  --btn-fab-width--lg: 6.25rem;
  --btn-color-border: var(--color-gray-3);

  /* Button Colors */
  --btn-color: var(--color-white);
  --btn-color-bg: var(--color-white);
  --btn-color-bg--primary: var(--color-primary);
  --btn-color-bg--primary--hover: var(--color-primary--light);
  --btn-color-bg--primary--focus: var(--color-primary--dark);
  --btn-color-bg--secondary: var(--color-secondary);
  --btn-color-bg--secondary--hover: var(--color-secondary--light);
  --btn-color-bg--secondary--focus: var(--color-secondary--dark);
  --btn-color-bg--info: var(--color-info);
  --btn-color-bg--info--hover: var(--color-info--light);
  --btn-color-bg--info--focus: var(--color-info--dark);
  --btn-color-bg--danger: var(--color-danger);
  --btn-color-bg--danger--hover: var(--color-danger--light);
  --btn-color-bg--danger--focus: var(--color-danger--dark);
  --btn-color-bg--warning: var(--color-warning);
  --btn-color-bg--warning--hover: var(--color-warning--light);
  --btn-color-bg--warning--focus: var(--color-warning--dark);
  --btn-color-bg--success: var(--color-success);
  --btn-color-bg--success--hover: var(--color-success--light);
  --btn-color-bg--success--focus: var(--color-success--dark);
  --btn-color-bg--facebook: var(--color-facebook);
  --btn-color-bg--facebook--hover: var(--color-facebook--light);
  --btn-color-bg--facebook--focus: var(--color-facebook--dark);
  --btn-color-bg--google: var(--color-google);
  --btn-color-bg--google--hover: var(--color-google--light);
  --btn-color-bg--google--focus: var(--color-google--dark);
  --btn-color-disabled: var(--color-white);
  --btn-color-disabled--outline: var(--color-gray-3);
  --btn-color-disabled-bg: var(--body-color-border);
  --btn-text-color-bg--hover: rgba(55, 65, 80, 0.03);
  --btn-text-color-bg--focus: rgba(55, 65, 80, 0.09);
  --btn-rounded-border-radius: 18px;

  /* Header */
  --header-color: var(--body-color);
  --header-color--dark: var(--body-color--heading);
  --header-color--active: var(--color-primary);
  --header-color-bg: var(--color-white);
  --header-color-border: var(--body-color-border);
  --header-color-link: var(--body-color-link);
  --header-padding: calc(var(--spacer) / 2) 0;
  --header-shadow: 0 2px 2px -1px rgba(30, 38, 51, 0.1);
  --header-height: 4.125rem;

  /* Footer */
  --footer-color: var(--body-color);
  --footer-color-bg: var(--body-color-bg);
  --footer-color-link: var(--body-color-link);

  /* Navigation */
  --navigation-mobile-width: 18.75rem;
  --navigation-font-weight: var(--font-weight--bold);
  --navigation-hamburger-span-width: 1.5rem;
  --navigation-hamburger-span-height: 2px;
  --navigation-hamburger-size: 2.625rem;
  --navigation-box-shadow: 4px 0 8px rgba(30, 38, 51, 0.1);

  /* Card */
  --card-color: var(--color-gray-7);
  --card-color--dark: var(--color-gray-10);
  --card-color--light: var(--color-gray-5);
  --card-color-bg: var(--color-white);
  --card-color-border: var(--body-color-border);
  --card-shadow: var(--depth-1);
  --card-shadow--hover: var(--depth-2);
  --card-border-radius: var(--border-radius--lg);
  --card-padding: var(--spacer);
  --card-padding--sm: calc(var(--spacer) / 2);
  --card-padding--lg: calc(var(--spacer) * 1.5);
  --card-padding--xl: calc(var(--spacer) * 3);
  --card-transition: var(--transition);
  --card-divider-height: 0.25rem;

  /* Thumb */
  --thumb-border-radius: 50%;
  --thumb-size: 2.375rem;
  --thumb-size--xxs: 0.75rem;
  --thumb-size--xs: 1.75rem;
  --thumb-size--sm: 2rem;
  --thumb-size--md: 4.5rem;
  --thumb-size--lg: 9.25rem;
  --thumb-size-ratio-16-9: calc(100% / (16 / 9));

  /* Badge */
  --badge-color: var(--color-white);
  --badge-color-bg: var(--color-white);
  --badge-color-bg--secondary: var(--color-secondary);
  --badge-border-radius: 4px;
  --badge-color-disabled--outline: var(--color-gray-3);
  --badge-color-disabled-bg: var(--body-color-border);
  --badge-color-bg--primary: var(--color-primary);
  --badge-color-bg--info: var(--color-info);
  --badge-color-bg--danger: var(--color-danger);
  --badge-color-bg--warning: var(--color-warning);
  --badge-color-bg--success: var(--color-success);
  --badge-shadow: var(--depth-1);
  --badge-size: 2.375rem;
  --badge-size--xs: 0.75rem;
  --badge-size--sm: 2rem;
  --badge-size--md: 4rem;
  --badge-size--lg: 6.25rem;
  --badge-padding--sm: calc(var(--spacer) / 3) calc(var(--spacer) / 2);
  --badge-padding--squared--xs: calc(var(--spacer) / 4) calc(var(--spacer) / 4);

  /* Modal */
  --modal-color: var(--card-color);
  --modal-color--dark: var(--card-color--dark);
  --modal-color--light: var(--card-color--light);
  --modal-color-bg: var(--card-color-bg);
  --modal-color-border: var(--card-color-border);
  --modal-border-radius: var(--card-border-radius);
  --modal-padding: var(--card-padding);
  --modal-padding--lg: var(--card-padding--lg);
  --modal-sm: 30rem;
  --modal-md: 40rem;
  --modal-lg: 50rem;
  --modal-xl: 60rem;

  /* Dropdown */
  --dropdown-size: 12rem;
  --dropdown-size-md: 16rem;
  --dropdown-size-lg: 20rem;
  --dropdown-shadow: var(--depth-4);
  --dropdown-border-radius: var(--card-border-radius);
  --dropdown-color: var(--card-color);
  --dropdown-color-bg: var(--card-color-bg);
  --dropdown-color-border: rgba(0, 0, 0, 0.1);
  --dropdown-item-color: var(--card-color--dark);
  --dropdown-item-color-bg-hover: rgba(0, 0, 0, 0.025);
  --dropdown-item-color-focus: var(--color-secondary);
  --dropdown-item-color-bg-focus: var(--btn-color);
  --dropdown-z-index: var(--z-index-20);

  /* Notification */
  --notification-container-width: 20rem;
  --notification-z-index: var(--z-index-110);
  --notification-box-shadow: var(--depth-2);
  --notification-icon-size: 2rem;
  --notification-border-radius: var(--border-radius--lg);

  /* Progress */
  --progress-height: 4px;
  --progress-border-radius: 1em;
  --progress-color-bg: var(--body-color-border);
  --progress-color-bg-fill: var(--color-secondary);

  /* Tabs */
  --tabs-height: 3rem;

  /* Logo */
  --logo-width-max: 15rem;
  --logo-height-max: 3rem;

  /* Sidebar */
  --sidebar-color-border: var(--color-primary);
  --sidebar-color-active: var(--body-color--dark);
  --sidebar-item-color-border: var(--body-color-border);
  --sidebar-item-color-hover: var(--body-color--dark);

  /* Tooltip */
  --tooltip-color: var(--color-white);
  --tooltip-color-bg: var(--color-info);

  /* Tag */
  --tag-color: var(--color-white);
  --tag-color-bg: var(--color-info);
  --tag-border-radius: 3rem;

  /* Fillable */
  --fillable-padding: var(--spacer);
  --fillable-width: 40rem;
  --fillable-max-height: 40rem;
  --fillable-min-height: 4rem;

  /* Divider */
  --divider-bg: var(--color-white);

  /* Table */
  --table-color: var(--card-color);
  --table-color--light: var(--card-color--light);
  --table-color--dark: var(--card-color--dark);
  --table-color-bg: var(--card-color-bg);
  --table-color-bg--selected: var(--color-info-bg);
  --table-color-border: var(--card-color-border);
  --table-color-thead: var(--color-white);
  --table-color-thead-bg: var(--color-info);
  --table-border-radius: var(--border-radius);
  --table-padding: var(--card-padding);
  --table-padding-x: var(--spacer);
  --table-padding-y: var(--spacer);
  --table-shadow: var(--card-shadow);
  --table-shadow-hover: var(--depth-2);
  --table-min-width: var(--viewport-lg);

  /* Empty */
  --empty-border-radius: var(--border-radius--lg);
  --empty-shadow--hover: var(--depth-2);
  --empty-transition: var(--transition);
  --empty-shadow: var(--depth-1);
}
