.progress {
  display: block;
  height: var(--progress-height);
  border-radius: var(--progress-border-radius);
  background: var(--progress-color-bg);
  min-width: 4rem;

  &__in {
    display: block;
    height: var(--progress-height);
    background: var(--progress-color-bg-fill);
    border-radius: var(--progress-border-radius);
  }
}
